<template>
  <imp-panel>
    <h3 class="box-title" slot="header" style="width: 100%;">
      <el-date-picker
        v-model="searchKey"
        type="datetimerange"
        :picker-options="pickerOptions2"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        value-format="yyyy-MM-dd HH:mm:ss"
        @change="search($event)">
        align="right">
      </el-date-picker>
    </h3>
    <div slot="body">
      <div id="myChart" :style="{width: '1000px', height: '300px'}"></div>
    </div>
  </imp-panel>
</template>

<script>
  import panel from "../../components/panel.vue"
  import * as api from "../../api"
  import testData from "../../../static/data/data.json"
  import * as tongjiApi from '../../services/tongji'

  export default {
    components: {
      'imp-panel': panel
    },
    data(){
      return {
        currentRow: {},
        dialogVisible: false,
        dialogLoading: false,
        defaultProps: {
          children: 'children',
          label: 'name',
          id: "id",
        },
        roleTree: [],
        listLoading: false,
        tableData: {
          pagination: {
            total: 0,
            pageNo: 1,
            pageSize: 10,
            parentId: 0
          },
          rows: []
        },
         pickerOptions2: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        searchKey: '',
      }
    },
    methods: {
      search(target){
        this.loadData();
      },
      handleSelectionChange(val){

      },
    handleSizeChange(val) {
        this.tableData.pagination.pageSize = val;
        this.loadData();
      },
      handleCurrentChange(val) {
        this.tableData.pagination.pageNo = val;
        this.loadData();
      },
      loadData(){
          tongjiApi.userActivity({
            key: this.searchKey,
          })
          .then(res => {
              console.log(res);
            //初始化
            let myChart = this.$echarts.init(document.getElementById('myChart'));
            // 绘制图表
            myChart.setOption({
                title: {
                    text: '用户活跃度'
                },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    data:['呼叫用户总数','呼叫成功用户数','接通用户数','接通成功用户数']
                },
                grid: {
                    left: '3%',
                    right: '4%',
                    bottom: '3%',
                    containLabel: true
                },
                toolbox: {
                    feature: {
                        saveAsImage: {}
                    }
                },
                xAxis: {
                    type: 'category',
                    boundaryGap: false,
                    data: ['呼叫用户总数','呼叫成功用户数','接通用户数','接通成功用户数']
                },
                yAxis: {
                    type: 'value'
                },
                series: [
                    {
                        name:'用户活跃度',
                        type:'line',
                        stack: '总量',
                        data:[res.callUserNum,res.callSuccessUserNum,res.chatUserNum,res.chatSuccessUserNum]
                    },
                ]
            });
          });
      },

    },
    created(){
      this.loadData();
    },

  }
</script>
<style>
  .el-pagination {
    float: right;
    margin-top: 15px;
  }
</style>



